import { ResetPasswordPage } from './../index';
import { Component, OnInit, trigger, state, style, transition, animate, keyframes } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, ToastController, LoadingController, AlertController } from 'ionic-angular';

import { User } from '../../providers';
import { MainPage, SelectPlacaPage } from '../';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
  animations: [
 
    //For the logo
    trigger('flyInBottomSlow', [
      state('in', style({
        transform: 'translate3d(0,0,0)'
      })),
      transition('void => *', [
        style({transform: 'translate3d(0,2000px,0'}),
        animate('2000ms ease-in-out')
      ])
    ]),
 
    //For the background detail
    trigger('flyInBottomFast', [
      state('in', style({
        transform: 'translate3d(0,0,0)'
      })),
      transition('void => *', [
        style({transform: 'translate3d(0,2000px,0)'}),
        animate('1000ms ease-in-out')
      ])
    ]),
 
    //For the login form
    trigger('bounceInBottom', [
      state('in', style({
        transform: 'translate3d(0,0,0)'
      })),
      transition('void => *', [
        animate('2000ms 200ms ease-in', keyframes([
          style({transform: 'translate3d(0,2000px,0)', offset: 0}),
          style({transform: 'translate3d(0,-20px,0)', offset: 0.9}),
          style({transform: 'translate3d(0,0,0)', offset: 1})
        ]))
      ])
    ]),
 
    //For login button
    trigger('fadeIn', [
      state('in', style({
        opacity: 1
      })),
      transition('void => *', [
        style({opacity: 0}),
        animate('1000ms 2000ms ease-in')
      ])
    ])
  ]
})
export class LoginPage implements OnInit {

  logoState: any = "in";
  cloudState: any = "in";
  loginState: any = "in";
  formState: any = "in";

  // The account fields for the login form.
  // If you're using the username field with or without email, make
  // sure to add it to the type
  account: { username: string, password: string, isOnline:boolean } = {
    username: '',
    password: '',
    isOnline: false
  };

  // Our translated text strings
  private loginErrorString: string;
  private httpErrorString: string;
  waitString: string;
  constructor(
    public navCtrl: NavController,
    public user: User,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public translateService: TranslateService,
    private alertCtrl: AlertController
  ) {

    this.translateService.get(['LOGIN_ERROR', 'HTTP_LOGIN_ERROR', 'WAIT']).subscribe((values) => {
      this.loginErrorString = values['LOGIN_ERROR'];
      this.httpErrorString = values['HTTP_LOGIN_ERROR'];
      this.waitString = values['WAIT'];
    });
   
  }

  ngOnInit() {
    if (localStorage.getItem('yourTokenKey') != null && localStorage.getItem('yourTokenKey').length > 0){
      this.user.validarLogin().subscribe((resp: any) => {
        if (resp != null && resp.isMobile && !resp.primeiroAcesso){
          this.user._user = resp;
          if (this.user._user != null && this.user._user != undefined){
            if(this.user._user.isMobile) {
              if(this.user._user.nivel == 6) {
                this.navCtrl.setRoot(SelectPlacaPage);
              }else {
                this.navCtrl.setRoot(MainPage);
              }
            }
          }
        }else {
          localStorage.removeItem('yourTokenKey');
        }
      },error => {
        localStorage.removeItem('yourTokenKey');
      });
    }
    
  }

  // Attempt to login in through our User service
  doLogin() {
    const data = {
      username: this.account.username.trim(),
      password: this.account.password,
      isOnline: this.account.isOnline
    }

    let loading = this.loadingCtrl.create({
      content: this.waitString
    });
    loading.present();

    this.user.login(data).subscribe((resp: any) => {
      if(resp.id_token){
        if(!resp.usuario.isMobile){
          let toast = this.toastCtrl.create({
            message: this.loginErrorString,
            duration: 3000,
            position: 'top'
          });
          toast.present();

        }else {
          console.log(this.account.isOnline);
          if(this.account.isOnline){
            localStorage.setItem('yourTokenKey', resp.id_token);
          }else {
            sessionStorage.setItem('yourTokenKey', resp.id_token);
          }
          this.user._user = resp.usuario;
          
          if (resp.usuario.primeiroAcesso) {
            this.navCtrl.push(ResetPasswordPage);
          }else {
            if(resp.usuario.nivel == 6) {
              this.navCtrl.setRoot(SelectPlacaPage);
            }else {
              this.navCtrl.setRoot(MainPage);
            }
            //this.navCtrl.setRoot(SelectPlacaPage);
          }
          
        }
        // this.navCtrl.setRoot(SelectPlacaPage);
        
        
      }
    }, (err) => {
      loading.dismiss();
      if(err.status === 401) {
        let toast = this.toastCtrl.create({
          message: this.loginErrorString,
          duration: 3000,
          position: 'top'
        });
        toast.present();
      }else {
        let toast = this.toastCtrl.create({
          message: this.httpErrorString,
          duration: 3000,
          position: 'top'
        });
        toast.present();
      }
      // Unable to log in
      
    },() => {
      loading.dismiss();
    });
  }
}



