import { Rastro } from './../../models/rastro';
import { FiltroVeiculo } from './../../models/filtro-veiculo';
import { DataStore } from './../../models/data-store';
import { Marcador } from './../../models/marcador';
import { LoginPage } from './../login/login';
import { MyApp } from './../../app/app.component';
import { Cliente } from './../../models/cliente';

import { TranslateService } from '@ngx-translate/core';
import { SelectClientePage, Tab1Root } from './../index';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController, ToastController } from 'ionic-angular';

import { Item } from '../../models/item';
import { Items, User } from '../../providers';
import { SearchService } from '../../providers/api/search.service';
import { VeiculoService } from '../../providers/api/veiculo.service';
import { MarkerOptions, HtmlInfoWindow, GoogleMapsEvent } from '@ionic-native/google-maps';
import { VelocidadePipe } from '../../pipes/velocidade.pipe';
import { DatePipe } from '@angular/common';
import { DistanciaPipe } from '../../pipes/distancia.pipe';

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html'
})
export class SearchPage implements OnInit {

  currentItems: any = [];
  form: FormGroup;
  clientString: string;
  cliente: Cliente = null;
  waitString: string;
  msgNoVeiculo: string;
  subSettings: any = SelectClientePage;
  directionString: string;
  dateTimeString: string;
  placaString: string;
  speedString: string;
  locationString: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public items: Items,
    public formBuilder: FormBuilder,
    public modalCtrl: ModalController,
    public translateService: TranslateService,
    public user: User,
    public app: MyApp,
    public searchService: SearchService,
    public loadingCtrl: LoadingController,
    public veiculoService: VeiculoService,
    public toastCtrl: ToastController,
    private distanciaPipe: DistanciaPipe,
    private datePipe: DatePipe,
    private velocidadePipe: VelocidadePipe
  ) {
    this.translateService.get(['CLIENT', 'WAIT', 'MSG_VEICULO_ERROR_VAZIO',"PLATE","DIRECTION","DATETIME","SPEED","LOCATION"]).subscribe((values) => {
      this.clientString = values['CLIENT'];
      this.waitString = values['WAIT'];
      this.msgNoVeiculo = values['MSG_VEICULO_ERROR_VAZIO'];
      this.placaString = values["PLATE"];
      this.directionString = values["DIRECTION"];
      this.dateTimeString = values["DATETIME"];
      this.speedString = values["SPEED"];
      this.locationString = values["LOCATION"];
    });
  }


  ngOnInit() {
    this.form = this.formBuilder.group({
      idVei: new FormControl({value: '', disabled: false}),
      ignicao: new FormControl(),
      inter: new FormControl(),
      plcVei: new FormControl({value: '', disabled: false}),
      tecnologia: new FormControl({value: '', disabled: false}),
      pontoMP: new FormControl({value: '', disabled: false}),
      equipamento: new FormControl({value: '', disabled: false}),
      clienteId: new FormControl({value: '', disabled: false}),
      clienteMae: new FormControl({value: '', disabled: false}),
      dscVei: new FormControl({value: '', disabled: false}),
      ativoCMae: new FormControl({value: '', disabled: false}),
    });



    const user = this.user._user;
    const filtro = new FiltroVeiculo();
    if (user.clienteId !== null) {
      if (user.clienteId === 1) {
        
      } else {
        filtro.clienteIdFilter = user.clienteFilho;
      }
      filtro.lstClienteId = user.clienteFilho;
    }
    if(user.clienteId !== 1){
      this.filtroVeiculos(filtro);
    }
  }

  /**
   * Perform a service for the proper items.
   */
  getItems(ev) {
    let val = ev.target.value;
    if (!val || !val.trim()) {
      this.currentItems = [];
      return;
    }
    this.currentItems = this.items.query({
      name: val
    });
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    this.navCtrl.push('ItemDetailPage', {
      item: item
    });
  }


  openModalCliente(){
    let loading = this.loadingCtrl.create({
      content: this.waitString
    });
    loading.present();
    this.searchService.getClientes()
    .subscribe((res:any) => {
      loading.dismiss();
      const profileModal = this.modalCtrl.create(SelectClientePage, { clientes: res });
      profileModal.onDidDismiss(data => {
        this.cliente = data;
      })
      profileModal.present();
    },error => {
      loading.dismiss();
      if(error.error.exception === 'io.jsonwebtoken.ExpiredJwtException') {
        this.user.logout();
        this.app.getRootNav().setRoot(LoginPage);
      }
    },() => {
    });
  }

  logout() {
    this.user.logout();
    this.app.getRootNav().setRoot(LoginPage);
  }

  filtrar() {
    const filtro = this.form.value;

    const filtroVeiculo = new FiltroVeiculo();
    
    filtroVeiculo.clientIdMae = filtro.clienteId;
    filtroVeiculo.descricao = filtro.dscVei;
    filtroVeiculo.equipamento = filtro.equipamento;
    filtroVeiculo.veiId = filtro.idVei;
    filtroVeiculo.ignicao = filtro.ignicao;
    filtroVeiculo.tipo = filtro.inter;
    filtroVeiculo.placa = filtro.plcVei;
    filtroVeiculo.ponto = filtro.pontoMP;
    filtroVeiculo.tecnologiaId = filtro.tecnologia;

    if (this.cliente != null) {
      filtroVeiculo.clientIdMae = this.cliente.id;
    }

    if (this.user._user.clienteId !== null) {
      filtroVeiculo.lstClienteId = this.user._user.clienteFilho;
    }
    
    this.veiculoService.ultimoFiltro = filtroVeiculo;
    this.filtroVeiculos(filtroVeiculo);
    
  }


  filtroVeiculos(filtro: any) {
    this.veiculoService.pararTimeDataStore();
    let loading = this.loadingCtrl.create({
      content: this.waitString
    });
    loading.present();
    this.veiculoService.ultimoFiltro = filtro;
    this.veiculoService.getVeiculos(filtro).subscribe((data:any) => {
      loading.dismiss();
      if(data.length) {
        
        this.veiculoService.veiculos = data;
        this.veiculoService.limparMarcadores();
        this.veiculoService.veiculos.forEach((veiculo: DataStore) => {
          const marcador = new Marcador(veiculo.veiculoId, veiculo)
          if(this.veiculoService.mapa != null){

            let path = 'assets/icon/car.png';
            if (veiculo.ignicao) {
              path = 'assets/icon/car-ign.png';
            }
            const iconVeiculo = {
              url: path,
              size: {
                width: 30,
                height: 30
              }
            }
            const direction = ((veiculo.direcao) * 48);
            let metaMarker: MarkerOptions = {
              position: {lat: veiculo.latitude, lng: veiculo.longitude},
              icon: iconVeiculo,
              flat: true,
              rotation: direction
            };
            this.veiculoService.mapa.addMarker(metaMarker).then(mark => {
              mark.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
                let htmlInfoWindow = this.getWindowsInfo(marcador.veiculo);
                htmlInfoWindow.open(mark);
              });
              marcador.marker = mark;
              
            });
          }
          this.veiculoService.marcadores.set(veiculo.veiculoId, marcador);
          
          
        });
        this.veiculoService.tabRef.select(0);
        this.veiculoService.tabRef.getByIndex(0).goToRoot({});
        
      }else {
        let toast = this.toastCtrl.create({
          message: this.msgNoVeiculo,
          duration: 3000,
          position: 'top'
        });
        toast.present();
        this.veiculoService.veiculos = [];
        if(this.veiculoService.mapa != null){
          this.veiculoService.limparMarcadores();
        }
      }

    },error => {
      loading.dismiss();
      if(error.error.exception === 'io.jsonwebtoken.ExpiredJwtException' ||
          error.error.error === 'Unauthorized') {
        this.user.logout();
        this.app.getRootNav().setRoot(LoginPage);
      }
    },() => {
      //loading.dismiss();
      this.veiculoService.iniciarTimeDataStore();
    });
  }

  getWindowsInfo(veiculo:DataStore): HtmlInfoWindow {
    let dir = '';
    if (this.translateService.getBrowserLang() === 'pt' ||
      this.translateService.getBrowserLang() === 'pt-br') {
      switch (veiculo.direcao) {
        case 0: dir = 'Norte'; break;
        case 1: dir = 'Nordeste'; break;
        case 2: dir = 'Leste'; break;
        case 3: dir = 'Sudeste'; break;
        case 4: dir = 'Sul'; break;
        case 5: dir = 'Sudoeste'; break;
        case 6: dir = 'Oeste'; break;
        case 7: dir = 'Noroeste'; break;
      }

    } else {
      switch (veiculo.direcao) {
        case 0: dir = 'North'; break;
        case 1: dir = 'Northeast'; break;
        case 2: dir = 'East'; break;
        case 3: dir = 'Southeast'; break;
        case 4: dir = 'South'; break;
        case 5: dir = 'South-west'; break;
        case 6: dir = 'West'; break;
        case 7: dir = 'Northwest'; break;
      }
    }
    const velocidade = this.velocidadePipe.transform(veiculo.velocidade);
    const location = veiculo.posicao;
    const dh = this.datePipe.transform(veiculo.dh, 'dd/MM/yyyy HH:mm:ss');
    let htmlInfoWindow = new HtmlInfoWindow();
    let frame: HTMLElement = document.createElement('div');
    frame.innerHTML = [
      '<div>' + this.directionString + ': ' + dir + '</div>',
      '<div>' + this.dateTimeString + ': ' + dh + '</div>',
      '<div>' + this.placaString + ': ' + veiculo.placa + '</div>',
      '<div>' + this.speedString + ': ' + velocidade + '</div>',
      '<div>Latitude: ' + veiculo.latitude.toFixed(5) + '</div>',
      '<div>Longitude: ' + veiculo.longitude.toFixed(5) + '</div>',
      '<div>' + this.locationString + ': ' + location + '</div>',
    ].join("");
    htmlInfoWindow.setContent(frame, {
      width: "250px",
      height: "150px"
    });
    return htmlInfoWindow;
  }

}



